import React, { FC, useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useDispatch, useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Alert, Form, Modal, ModalBody } from 'reactstrap';
import {
  ACTION_EVENTS,
  alertManager,
  AlertProps,
  AlertType,
  dispatchClose,
  EditViewProps,
  ERROR,
  InputGroupInline,
  InputType,
  isEmpty,
  ProcessBar,
  progressActions,
  showToast,
  SIMPLE,
  WindowComponent,
} from 'summer';
import { hasEmpresa } from '../../../../dependencies/empresaDependency';
import { hasEmpresaMCG } from '../../../../dependencies/empresaMCGDependency';
import { getDefaultReportBasicValues } from '../../../../models/dtos/ger/reportBasicValues';
import { getDefaultReportViewOptions, ReportViewOptions } from '../../../../models/dtos/ger/reportViewOptions';
import { ImportacaoResult, ImportarMCP } from '../../../../models/dtos/mcg/importarMCP';
import { integrarMCP } from '../../../../services/mcg.service';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';
import ReportForm from '../../../components/report/ReportForm';
import ResultadoIntegracao from '../../../components/resultadoIntegracao/ResultadoIntegracao';
import { PendenciasRV, REPORT_TITLE } from '../../mfp/report/pendencias/pendenciasRV';

interface IntegracaoMCPProcessEditViewProps extends EditViewProps<ImportarMCP> {}

const REPORT_EVENT: string = 'printPendenciasIntegracao';

const DEFAULT_VALUES: PendenciasRV = {
  ...getDefaultReportBasicValues(REPORT_TITLE),
  cabecalhoTxt: 'Mensagem',
  gerarCabecalhoPadraoAdministradora: true,
  title: 'Resultado da Integração de Controle Patrimonial',
};

const DEFAULT_OPTIONS: ReportViewOptions = {
  ...getDefaultReportViewOptions(REPORT_EVENT, SIMPLE.value),
  exibirConfiguracaoDadosEmpresa: false,
};

const IntegracaoMCPProcessView: FC<IntegracaoMCPProcessEditViewProps & RouteComponentProps> = props => {
  const [showResultView, setShowResultView] = useState(false);
  const [listResult, setListResult] = useState(undefined);
  const [showPrintModal, setShowPrintModal] = useState(false);

  const formProps = useForm<ImportarMCP>({ mode: 'onBlur' });
  const { handleSubmit, ...allFormProps } = useForm<PendenciasRV>({ mode: 'onBlur' });

  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const dispatch = useDispatch();

  useEffect(
    () => {
      if (!isEmpty(globalReducer.globalParameter.empresaMCG) && !isEmpty(globalReducer.globalParameter.empresa)) {
        const {
          empresaMCG: { refAtual, refFinal },
          empresa: { id },
        } = globalReducer.globalParameter;

        setTimeout(() => {
          formProps.setValue('idGerEmpresa', id);
          formProps.setValue('refInicial', refAtual);
          formProps.setValue('refFinal', refFinal);
        }, 500);
      }
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  function callImportacao(data: ImportarMCP, confirmado: boolean = false): void {
    data.confirmado = confirmado;
    integrarMCP(data, {
      errorFunction: mensagem => {
        showToast(mensagem, ERROR);
        dispatch(progressActions.hideProgress());
      },
      thenFunction: (result: ImportacaoResult) => {
        dispatch(progressActions.hideProgress());
        importResultEvent(result);
      },
    });
  }

  function handleImport(data: ImportarMCP): void {
    dispatch(
      progressActions.showProgress({
        animated: true,
        className: 'progress-bar-rounded progress-animated-alt',
        color: 'first',
        label: 'Aguarde processando...',
        striped: true,
        value: 100,
      })
    );
    callImportacao(data);
  }

  function importResultEvent(result: ImportacaoResult): void {
    if (result.encerramento) {
      const params: AlertProps = {
        message: result.encerramento[0].message,
        onOkClick: () => callImportacao(formProps.getValues(), true),
        type: AlertType.WARNING_YES_NO,
      };
      alertManager.emit(ACTION_EVENTS.SHOW, params);
    } else {
      if (result && result.fracasso.length === 0 && result.sucesso.length === 0) {
        const params: AlertProps = {
          message: 'Não existem lançamentos para os bens desta empresa no período informado.',
          type: AlertType.INFORMATION,
        };
        alertManager.emit(ACTION_EVENTS.SHOW, params);
      } else {
        setListResult(result);
        setShowResultView(true);
      }
    }
  }

  function handlePrint(): void {
    setShowPrintModal(true);
  }

  function addExtraParameters(formData: any): any {
    formData.erros = listResult.fracasso.map(it => it.message);
    return formData;
  }

  const onClose = (goBack: boolean = true) => {
    dispatchClose(props, props.history, goBack);
  };

  const handleCancel = () => onClose();

  const defaultWidth: number = 150;

  return (
    <WindowComponent
      title="Integração de Controle Patrimonial"
      useGlobalParameter={false}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasEmpresaMCG(globalReducer.globalParameter)]}
    >
      {showResultView ? (
        <ResultadoIntegracao
          {...props}
          errorList={listResult.fracasso}
          successList={listResult.sucesso}
          printReport={handlePrint}
          tabFailure="Falha"
          tabSuccess="Sucesso"
        />
      ) : (
        <Form>
          <InputGroupInline formProps={formProps} id="idGerEmpresa" hidden={true} />
          <InputGroupInline
            formProps={{ ...formProps, validation: { required: true } }}
            id="refInicial"
            label="Referência Inicial"
            labelWidth={defaultWidth}
            placeholder="Informe a Referência Inicial"
            isPrependLabel={true}
            type={InputType.REFERENCE}
          />
          <InputGroupInline
            formProps={{ ...formProps, validation: { required: true } }}
            id="refFinal"
            label="Referência Final"
            labelWidth={defaultWidth}
            placeholder="Informe a Referência Final"
            isPrependLabel={true}
            type={InputType.REFERENCE}
          />
          <Alert color="danger" className="text-center">
            Atenção: Todos os lançamentos previamente importados no período selecionado, e os encerramentos de contas de resultado posteriores à
            referência Inicial serão excluídos.
          </Alert>
          <ProcessBar handleSubmit={formProps.handleSubmit(handleImport)} handleClose={handleCancel} />
        </Form>
      )}

      <Modal isOpen={showPrintModal}>
        <ModalBody>
          <ReportForm
            routeProps={{ ...props }}
            handleSubmit={handleSubmit}
            formProps={{ ...allFormProps }}
            reportBasicValues={DEFAULT_VALUES}
            reportViewOptions={DEFAULT_OPTIONS}
            addExtraParameters={addExtraParameters}
            extraConfigOpened={true}
          />
        </ModalBody>
      </Modal>
    </WindowComponent>
  );
};

export default IntegracaoMCPProcessView;
